import Vue from 'vue'
import Router from 'vue-router'
import Footer from '@/components/Footer'
import Header from '@/components/Header'
import Home from '@/page/home/index'
import XCR from '@/page/xcr/index'
import Time from '@/page/time/index'
import Partner from '@/page/partner/index'
import Newsreport from '@/page/newsreport/index'
import Abouts from '@/page/aboutus/index'
Vue.use(Router)

export default new Router({
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      components: {
        body: Home,
        header: Header,
        footer: Footer
      }
    },
    {
      path: '/xcr',
      name: 'xcr',
      components: {
        body: XCR,
        header: Header,
        footer: Footer
      }
    },
    {
      path: '/time',
      name: 'time',
      components: {
        body: Time,
        header: Header,
        footer: Footer
      }
    }
    ,
    {
      path: '/partner',
      name: 'partner',
      components: {
        body: Partner,
        header: Header,
        footer: Footer
      }
    }
    ,
    {
      path: '/newsreport',
      name: 'newsreport',
      components: {
        body: Newsreport,
        header: Header,
        footer: Footer
      }
    },
    {
      path: '/aboutus',
      name: 'aboutus',
      components: {
        body: Abouts,
        header: Header,
        footer: Footer
      }
    }
  ]
})



