<template>
  <div class="header" :class="{fixed:isfixed}">
    <div class="container">
      <img src="../assets/homelogo2.png" alt="" class="logo">
      <div class="nav">
        <router-link to="/">首页</router-link>
        <router-link to="/time">大事记</router-link>
        <router-link to="/newsreport">媒体报道</router-link>
        <router-link to="/partner">合作伙伴</router-link>
        <router-link to="/xcr">小虫人</router-link>
        <router-link to="/aboutus">关于我们</router-link>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "headercm",
    data() {
      return {
        isfixed: false
      };
    },
    mounted() {
      window.addEventListener("scroll", e => {
        if (window.scrollY < 10) {
          this.isfixed = false;
        } else {
          this.isfixed = true;
        }
      });
    }
  };
</script>

<style lang="scss" scoped>
  @import "./Header.scss";
</style>


