<template>
  <div class="pagenewsreport">
    <div class="banner">
      <img src="../../assets/npbg.png" alt="">
    </div>
    <div class="sel s1">
      <div class="container">
        <!-- <div class="loading" v-if="inload" style="text-align:center">loading...</div> -->
        <div class="s1itemlist">
          <div class="s1item clearfix" v-for="i in list" v-if="list">
            <div class="s1itemup clearfix">
              <div class="date">
                <div class="month">{{i.newsTime.month+1}}</div>
                <div class="day">{{i.newsTime.date}}</div>
              </div>
              <div class="title">{{i.title}}
                <!-- <span>来源:{{i.source}}</span> -->
              </div>
            </div>
            <div class="s1itemdown clearfix" v-bind:class="{showall:i.showall}">
              <img :src="i.picture" alt="" class="showimg">
              <div class="showinfo" v-html="i.content"></div>
              <div class="showmore" @click="change(i)">显示更多</div>
              <div class="hidemore" @click="change(i)">
                <img src="../../assets/shouqi.png" alt=""> 收起
              </div>
            </div>
          </div>

        </div>
        <el-pagination v-if="list&&list.length>0" prev-text="上一页" next-text="下一页" @current-change="handleCurrentChange" layout="prev, pager, next" :page-size="pagesize" :total="pagetotal" :current-page.sync="pagenow">
        </el-pagination>
      </div>

    </div>
  </div>
  </div>
</template>

<script>
  import { appconfig } from "../../config";

  export default {
    name: "newspage",
    data() {
      return {
        pagetotal: 0,
        pagesize: 5,
        pagenow: 0,
        list: [],
        inload: true
      };
    },
    created() {
      this.fetchData();
    },
    methods: {
      change(i) {
        i.showall = !i.showall;
      },
      handleCurrentChange(val) {
        this.fetchData(val);
      },
      fetchData(pg) {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        pg = pg || 1;
        this.pagenow = pg;
        this.inload = true;
        this.$http
          .get(
            appconfig.gmp +
              "/operation/getNewListForH5?type=0&status=0&pageSize=5&pageNum=" +
              pg
          )
          .then(
            response => {
              response.data.data.list.forEach(i => (i.showall = false));
              this.list = response.data.data.list;
              this.pagetotal = response.data.data.total * 1;
              this.inload = false;
            },
            response => {
              // this.list = [];
              // error callback
            }
          );
      }
    }
  };
</script>
<style lang="scss" scoped>
  @import "../../../theme/pagination.css";
  @import "./index.scss";
</style>



