<template>
  <div class="pagepartner">
    <div class="banner">
      <img src="../../assets/ptbg.png" alt="">
    </div>
    <div class="sel s1" v-for="(i,index) in list" v-if="index!=0">
      <div class="container">
        <div class="title">
          {{i.title}}
        </div>
        <el-carousel height="470px" indicator-position="outside" interval=4000000>
          <el-carousel-item v-for="ii in i.data">
            <div class="s1item clearfix">
              <div class="s1itembox" v-for="iii in ii">
                <img :src="iii.img" alt="">
                <div class="name">{{iii.title}}</div>
              </div>
            </div>
          </el-carousel-item>
        </el-carousel>
      </div>
    </div>

  </div>
</template>

<script>
  import list from "./list";
  export default {
    name: "partnerpage",
    data() {
      return {
        list: list
      };
    }
  };
</script>
<style lang="scss" scoped>
  @import "./index.scss";
</style>



